var that;
export default {
	data() {
		return {
			imgList: [],
			imgdetaillist: [],
			cover: '',
			codeb: '',
			zheng: '',
			index: ''
		};
	},
	methods: {
		async ChooseImage(e) {
			var that = this;
			var name = e.currentTarget.dataset.name;
			let uploader = new this.$Uploader();
			let path_arr = await uploader.choose_and_upload(1);
			// this.imgList = this.imgList.concat(path_arr[0]);
			that[name] = path_arr[0];
			console.log('cover', that[name]);
		},
		async ChooseImagelist(e) {
			var that = this;
			var name = e.currentTarget.dataset.name;
			let uploader = new this.$Uploader();
			let path_arr = await uploader.choose_and_upload(1);
			that[name] = that[name].concat(path_arr[0]);
			console.log(name, that[name]);
		},
		DelImg(e) {
			var that = this;
			let index = e.currentTarget.dataset.index;
			var name = e.currentTarget.dataset.name;
			uni.showModal({
				title: '提示',
				content: '确定要删除该图片吗？',
				cancelText: '取消',
				confirmText: '确定',
				success(res) {
					if (res.confirm) {
						that[name] = '';
						// that.imgList = that.imgList.splice(index,1)
					}
				}
			});
		},
		DelImglist(e) {
			var that = this;
			console.log(that.api);
			var index = e.currentTarget.dataset.index;
			var name = e.currentTarget.dataset.name;
			console.log('name', name);
			console.log('index', index);
			console.log('that[name]', that[name]);
			uni.showModal({
				title: '提示',
				content: '确定要删除该图片吗？',
				cancelText: '取消',
				confirmText: '确定',
				success(res) {
					if (res.confirm) {
						let list = that[name];
						list.splice(index, 1);
						that[name] = list;
						console.log('list', list);
					}
				}
			});
		},
		toSubmit(e) {
			var that = this;
			var cover = that.cover;
			var imgList = that.imgList;
			var imgdetaillist = that.imgdetaillist;
			var name = e.detail.value.name;
			var virtual_sales = e.detail.value.virtual_sales;
			var price = e.detail.value.price;
			if (cover == '') {
				that.$alert('请上传商品封面');
				return false;
			}
			if (imgList.length == 0) {
				that.$alert('请上传商品轮播图');
				return false;
			}
			if (imgdetaillist.length == 0) {
				that.$alert('请上传商品详情图');
				return false;
			}
			if (name == '' || price == '' || virtual_sales == '') {
				that.$alert('请完善信息');
				return false;
			}
			that.$request
				.request({
					url: that.api.anchor_order.anchor_goods_add,
					method: 'POST',
					data: {
						name: name,
						cover: cover,
						price: price,
						slider: imgList,
						virtual_sales: virtual_sales,
						detail_image: imgdetaillist
					},
					Toast: '正在提交'
				})
				.then(res => {
					console.log(res);
					uni.showToast({
						title: '提交成功！',
						icon: 'success',
						duration: 1200
					});
					setTimeout(function() {
						uni.navigateBack({
							delta: 1
						});
					}, 1500);
				});
		}
	},
	onLoad() {},
	onUnload() {},
	onHide() {}
};
